.slider {
  height: 466px;
  margin-bottom: 40px;

  @media screen and (max-width: 319px) {
    height: 600px;
  }

  @media screen and (min-width: 480px) {
    margin-top: -845px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 80px;
    margin-top: -835px;
  }

  @media screen and (min-width: 1200px) {
    height: 370px;
    margin-bottom: 100px;
    margin-top: -570px;
  }

  position: relative;
  overflow: hidden;
  margin-top: -870px;

  display: flex;

  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: row nowrap;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
  -ms-flex-align: end;
  align-items: flex-end;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.slider__nav {
  display: flex;
  justify-content: center;
  width: 12px;
  height: 12px;
  margin: 0 18px;
  border-radius: 50%;
  z-index: 10;
  outline: 6px solid rgba(217, 217, 217, 1);
  outline-offset: -6px;
  box-shadow: 0 0 0 0 #333, 0 0 0 0 rgba(51, 51, 51, 0);
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.slider__nav:checked {
  -webkit-animation: check 0.4s linear forwards;
  animation: check 0.4s linear forwards;
  color: #333;
}

.slider__nav:checked:nth-of-type(1) ~ .slider__inner {
  left: 0%;
}
.slider__nav:checked:nth-of-type(2) ~ .slider__inner {
  left: -100%;
}
.slider__nav:checked:nth-of-type(3) ~ .slider__inner {
  left: -200%;
}

.slider__inner {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 300%;
  height: 100%;
  -webkit-transition: left 0s;
  transition: left 0s;

  @media screen and (min-width: 1200px) {
    -webkit-transition: left 0.4s;
    transition: left 0.4s;
  }

  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: row nowrap;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
}
.slider__contents {
  text-align: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-flex-flow: column nowrap;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.slider__photo {
  display: block;
  margin-bottom: 140px;
}

.slider__image {
  font-size: 2.7rem;
  color: #2196f3;
  border-radius: 50%;
  margin-bottom: 32px;
}

.slider__caption {
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 20px;
  color: #131313;

  margin-bottom: 12px;

  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
  }
}
.slider__txt {
  margin-bottom: 32px;
  font-size: 12px;
  line-height: 2.55;
  // white-space: pre-line;

  color: #fcfcfc;

  background-color: rgba(132, 161, 120, 1);
  border-radius: 10px;
  padding: 35px 39px;
  text-align: center;
  letter-spacing: initial;

  @media screen and (min-width: 768px) {
    padding: 28px 80px 29px 80px;
    font-size: 14px;
    line-height: 1.86;
    border-radius: 24px;
    font-weight: 500;
    white-space: normal;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 100px;
    margin-right: 100px;
    padding: 43px 109px;
    font-size: 16px;
  }
}

@-webkit-keyframes check {
  100% {
    outline-color: rgba(132, 161, 120, 1);
    box-shadow: 0 0 0 0 #333, 0 0 0 0 rgba(51, 51, 51, 0);
  }
}

@keyframes check {
  100% {
    outline-color: rgba(132, 161, 120, 1);
    box-shadow: 0 0 0 0 #333, 0 0 0 0 rgba(51, 51, 51, 0);
  }
}
